	font-family: "quickSand";
	src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format(truetype);
}

@font-face {
	font-family: "Space";
	src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format(truetype);
}

@font-face {
	font-family: "Unbound";
	src: url(${___CSS_LOADER_URL_REPLACEMENT_2___}) format(truetype);
}

@font-face {
	font-family: "Lilita";
	src: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

:root {
	--60Color: black;
	--30Color: #2f2f2f;
	--10Color: #66bb6a;
}

body {
	background: var(--60Color);
	display: grid;
	height: 100vh;
	grid-template-columns: 1fr 3fr 1fr;
	grid-template-rows: 0.5fr 4fr 0.5fr;
	font-family: "quickSand";
	color: white;
}

header {
	grid-area: 1/1/1/4;
	display: flex;
	padding-left: 20px;
	padding-top: 10px;
	padding-right: 20px;
}

header > span {
	align-self: center;
	font-family: "Unbound";
	font-size: 40px;
}

#to2 {
	color: var(--10Color);
}

header > img {
	height: 90px;
}

#settings {
	width: 100%;
	display: flex;
	justify-content: end;
	text-align: center;
	align-self: center;
	margin-right: 50px;
}

#newObjBtns {
	color: var(--10Color);
	align-self: center;
	margin-left: 37px;
	margin-top: 10px;
	font-weight: bold;
	font-family: "quickSand";
	cursor: pointer;
}

main {
	display: grid;
}

footer {
	position: absolute;
	right: 30px;
	bottom: 15px;
}

footer > span {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 12px;
}

footer > span > a {
	width: 27%;
	display: flex;
	align-items: center;
	text-decoration: none;
	color: inherit;
	justify-content: space-between;
	margin-left: 4px;
}

footer > span > a > img {
	margin-left: 4px;
	filter: invert(68%) sepia(4%) saturate(5571%) hue-rotate(75deg) brightness(95%) contrast(88%);
}
